import React, { Component } from 'react';
import LogoJs from "../Photos/logo_JS.jpg";
import LogoBS from "../Photos/logo_bootstrap.jpg";
import LogoC from "../Photos/logo_creativity.jpg";
import LogoCss from "../Photos/logo_css.png";
import LogoHtml from "../Photos/logo_html.png";
import LogoReact from "../Photos/logo_React.png";
import LogoRedux from "../Photos/logo_redux.png";

class Skills extends Component {
  render() {
    return (
      <div className="App SkillBg">
        
        <div className="SkillBgColour">
        <br></br>
            <h2>Skills</h2> 
            <div className="SkillContent">

                <div className="content1">
                    <img className="logoStyle" src={LogoJs} alt="Javascript" />
                    <h4>Javascript</h4>
                </div>

                <div className="content1">
                    <img className="logoStyle" src={LogoHtml} alt="HTML5" />
                    <h4>HTML5</h4>
                </div>

                <div className="content1">
                    <img className="logoStyle" src={LogoCss} alt="Css3" />
                    <h4>CSS3</h4>
                </div>

                <div className="content1">
                    <img className="logoStyle" src={LogoBS} alt="Bootstrap" />
                    <h4>Bootstrap</h4>
                </div>

                <div className="content1">
                    <img className="logoStyle" src={LogoReact} alt="ReactJS" />
                    <h4>ReactJS</h4>
                </div>

                <div className="content1">
                    <img className="logoStyle" src={LogoRedux} alt="Redux" />
                    <h4>Redux</h4>
                </div>

                <div className="content1">
                    <img className="logoStyle" src={LogoC} alt="Creativity" />
                    <h4>Creativity</h4>
                </div>
                
            </div>
            <br></br><br></br>
        </div>
      </div>
    )
  }
}

export default Skills;



