import React, { Component } from 'react';
import ProfilePic from "../Photos/profile_pic.jpg";

class About extends Component {
  render() {
    return (
      <div className="App">
        <div className="About">
            <div className="AboutMain">
                
                <div className="AboutText">
                    
                    <p> <h1 className="AboutHeader">About Myself</h1>
                        I am a front-end developer by my work and passion, I love creating interactive, cool, 
                        creative and beautiful apps. I have basically created apps using HTML5, CSS3, Javascript, 
                        Reactjs, Bootstrap and you can check them in the below section of my portfolio.<br></br><br></br>

                        I am basically a fun-loving, chilled person and like to hang out with friends and explore 
                        new places. I like singing, listening to music and watching cricket apart from coding of 
                        course.<br></br><br></br>

                        If you find my portfolio interesting enough and you want to work with me, do not hesitate 
                        to drop a mail at my email id mentioned in the contact section. You can also call me on my 
                        contact number mentioned.
                    </p>
                </div>
                

                <div><img src={ProfilePic}/></div>

            </div>
        </div>
      </div>
    )
  }
}

export default About;


